@import "coin/bitcoin";


.icon {
  &.cryptoCoin {
    display: inline-block;

    &.mini {
      width: 10px;
      height: 10px;
      background-size: 10px auto;
    }

    &.small {
      width: 18px;
      height: 18px;
      background-size: 18px auto;
    }

    &.medium {
      width: 25px;
      height: 25px;
      background-size: 25px auto;
    }

    &.large {
      width: 50px;
      height: 50px;
      background-size: 50px auto;
    }

    &.big {
      width: 100px;
      height: 100px;
      background-size: 100px auto;
    }


    &.btc {
      @include bitcoin;
    }
  }
}


